import React from 'react';
import Home from './Js/Home';
import Work from './Js/work';
import AboutMe from './Js/aboutMe';
import '../src/css/style.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";


function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path="/work">
            <Work/>
          </Route>
          <Route path="/about-me">
            <AboutMe/>
          </Route>
          <Route path="/">
            <Home/>
          </Route>
        </Switch>
        <Home/>
      </Router>
    </div>
  );
}

export default App;
